"use client";

import { Line } from "react-chartjs-2";
import "chart.js/auto";

export default function StockChart() {
  // ข้อมูลสำหรับกราฟหุ้น
  const data = {
    labels: ["11:00", "11:03", "11:06", "11:09", "11:12", "11:15", "11:18"],
    datasets: [
      {
        label: "APPL Price",
        data: [230, 240, 235, 250, 245, 260, 255],
        borderColor: "#00ff00",
        backgroundColor: "rgba(0, 255, 0, 0.2)",
        fill: true,
        tension: 0.4, // เส้นโค้งเล็กน้อย
      },
    ],
  };

  return (
    <div className="p-4 bg-purple-700 rounded-xl text-white shadow-lg w-full max-w-2xl">
      {/* ชื่อหุ้นและราคา */}
      <div className="mb-2">
        <h2 className="text-3xl font-bold text-purple-300">APPL</h2>
        <p className="text-4xl font-bold">$245.41 <span className="text-lg">USD</span></p>
        <p className="text-lg text-gray-300">≈ 8,248.23 THB</p>
        <div className="flex items-center space-x-2 text-green-300 text-sm mt-1">
          <span className="w-2 h-2 bg-green-300 rounded-full"></span>
          <span>Open</span>
        </div>
      </div>

      {/* กราฟ */}
      <div className="bg-transparent">
        <Line data={data} />
      </div>
    </div>
  );
}
